<template>
	<div>
		<div class="title mb-3">
			Data PTN/Prodi
		</div>
		
		<div class="subtitle-1 mb-2">
			Pilih Program  Studi untuk PTN Pilihan ke-1
		</div>
		<v-sheet class="blue lighten-4 pa-3">
			<ul>
				<li>
					Jumlah program studi yang dapat dipilih maksimal 2.
				</li>
				<li>
					Jumlah perguruan tinggi yang dapat dipilih maksimal 2.
				</li>
				<li>
					Siswa yang mendaftar beasiswa Bidikmisi tidak dapat memilih Universitas Islam Negeri (UIN), karena Bidikmisi tidak berlaku untuk UIN.
				</li>
			</ul>
		</v-sheet>

		<v-row>
			<v-col cols="12" md="4" sm="12">
				<div class="title mb-2">
					Daftar PTN
				</div>
				<ul class="list-ptn">
					<li class="pa-3 d-flex" v-for="(item, i) in dataUniv" :key="item.id">
						<div class="mr-3">
							<b>{{ i+1 }}</b>
						</div>
						<div>
							<div class="text-uppercase primary--text font-weight-bold">
								<a 
									@click="namaUniv = item.nama"
									href="javascript:void(0)">
									{{ item.nama }}
								</a>
							</div>
							<div>
								({{ item.provinsi.nama }})
							</div>
						</div>
					</li>
				</ul>
			</v-col>
			<v-col 
				v-if="namaUniv === ''"
				cols="12" md="8" sm="12">
				<v-alert type="info">
					Silahkan pilih UNIVERSITAS terlebih dahulu
				</v-alert>
			</v-col>
			<v-col 
				v-else
				cols="12" md="8" sm="12">
				<div class="title mb-2">
					Daftar Program Studi untuk 
					<span class="text-uppercase">institut pertanian bogor</span>
				</div>

				<v-btn
					depressed
					color="primary"
					class="mr-3"
					@click="kelompok = 'SAINTEK'"
					small>
					saintek
				</v-btn>
				<v-btn
					depressed
					color="orange"
					@click="kelompok = 'SOSHUM'"
					dark
					small>
					soshum
				</v-btn>

				<v-simple-table dense class="table-prodi mt-3">
					<thead class="blue lighten-4">
						<tr>
							<td rowspan="2">Program Studi</td>
							<td rowspan="2">D.T</td>
							<td colspan="2" class="text-center">Pilihan</td>
							<td rowspan="2" class="text-center">Portofolio</td>
						</tr>
						<tr>
							<td class="text-center">1</td>
							<td class="text-center">2</td>
						</tr>
					</thead>
					<tbody>
						<template v-for="(item, i) in dataProdi">
							<!-- <Prodi :item="item" :key="item.id" /> -->
							<tr :key="item.id">
								<td class="primary--text text-uppercase font-weight-bold">
									{{ item.prodi.nama }}
								</td>
								<td>{{ item.daya_tampung }}</td>
								<td class="text-center">
									<input
										type="radio" 
										:name="`pilihan1`" 
										@click="pilihProdi({
											ptn_id: item.id,
											pilihan_ke: 1
										})" />
								</td>
								<td class="text-center">
									<input 
										type="radio" 
										:name="`pilihan2`" 
										@click="pilihProdi({
											ptn_id: item.id,
											pilihan_ke: 2
										})" />
								</td>
								<td class="text-center">
									<input type="checkbox" name="portofolio" value="punya">
								</td>
							</tr>
						</template>
					</tbody>
				</v-simple-table>
			</v-col>
		</v-row>

		<div class="d-flex">
			<v-spacer></v-spacer>
			<v-btn
				text
				class="mr-3"
				@click="$emit('step', 4)"
				color="primary">
				kembali
			</v-btn>
			<v-btn
				depressed
				type="submit"
				@click.prevent="validate"
				color="primary">
				selanjutnya
			</v-btn>
			<!-- <v-btn
				depressed				
				@click="validate"
				color="primary">
				selanjutnya
			</v-btn> -->
		</div>
	</div>
</template>

<style scoped>
	.list-ptn{
		list-style: none;
		padding: 0;
		border-top: 1px solid silver;
		border-right: 1px solid silver;
		border-left: 1px solid silver;
		border-radius: 3px;
	}
	.list-ptn li{
		border-bottom: 1px solid silver;
	}	
	.table-prodi{
		border: 1px solid silver;
	}
	td{ padding: 8px 16px !important; }
</style>

<script>
	import { mapState } from 'vuex'
	import { uniqBy } from 'lodash'

	export default{
		data: () => ({
			dataPilihanPtn: [],
			valid: true,
			kelompok: '',
			namaUniv: '',
			requiredRule: [v => !!v || 'Data harus diisi']
		}),

		computed: {
			...mapState('dataPtn', ['dataPtn']),
			dataUniv() {
				return uniqBy(this.dataPtn, 'nama')
			},
			dataProdi: {
				get() {
					if (this.kelompok === '') {
						return this.dataPtn.filter(ptn => ptn.nama === this.namaUniv)
					}else{
						return this.dataPtn.filter(ptn => ptn.nama === this.namaUniv && ptn.prodi.kelompok === this.kelompok)
					}
				}
			}
		},

		methods: {
			pilihProdi(data){
				this.dataPilihanPtn[data.pilihan_ke-1] = data
			},
			validate() {		
				this.$store.dispatch('dataPtn/getDetailPtn', this.dataPilihanPtn)
				this.$store.commit('dataPeserta/updateDataPtn', this.dataPilihanPtn)
				this.$emit('step', 6)
			}
		}
	}
</script>